	module.exports = {"data":{"post":{"html":"<p>Yesterday Markus Gäfgen was <a href=\"http://www.expatica.com/germanymain.asp?pad=190,205,&amp;item_id=33079\">sentenced to life imprisonment</a> for the kidnap and murder of the 11 year old Jakob von Metzler in Frankfurt last summer. This would merely be local news, except that after Gäfgen's arrest, when it was still thought the boy was alive, Frankfurt police <a title=\"Deutsche Welle: Germany\" href=\"http://www.dw-world.de/english/0,3367,1432_A_936126_1_A,00.html\">threatened</a> the suspect with 'severe pain' (i.e. torture) if he did not reveal the location of the boy.</p>\n<p>The court ruled Gäfgen's police confession as inadmissible, and declared that the police conduct had done 'serious damage' to the German rule of law. However, it also ruled that the police conduct had no bearing on Gäfgen's guilt and declared the crime as extraordinary, ruling out parole after 15 years. </p>\n<p>I'm no student of the law, but it is interesting how the German court separated the extreme breach of constitutional rights from the criminal case. An American court may well have let Gäfgen walk away scot-free.</p>\n<p>Update: Added link to <a title=\"Deutsche Welle: Killer Convicted, Police Under Fire for Torture\" href=\"http://www.dw-world.de/english/0,3367,1432_A_936126_1_A,00.html\">Deutsche Welle</a>, which is more complete than the Expatica report.</p>","fields":{"title":"Life for a life","date":"29 Jul 2003","url":"https://papascott.de/2003-07-29-life-for-a-life/"}}},"pathContext":{"slug":"/2003-07-29-life-for-a-life/","prev":{"fields":{"title":"PHP5: Coming Soon to a Webserver Near You","date":"28 Jul 2003","slug":"/2003-07-28-php5-coming-soon-to-a-webserver-near-you/"}},"next":{"fields":{"title":"Open Source Flying","date":"29 Jul 2003","slug":"/2003-07-29-open-source-flying/"}}}}

